import * as React from 'react';
import {YesNoDialog} from "./yes-no-dialog";

export class UserListPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = UserListPanel.getInitialState();
    }

    static defaultProps = {
        url: '/api/users'
    };

    static getInitialState() {
        // allUsersusers
        // users
        // usersallUsers
        return {
            users: [
                {
                    id: 1,
                    name: '',
                    username: 'majiga2333',
                    type: 'admin', // admin / storeOwner / clerk
                }
            ],
            allUsers: [
                {
                    id: 1,
                    name: '',
                    username: 'majiga2333',
                    type: 'admin', // admin / storeOwner / clerk
                }
            ]
        }
    };


    static getTypeString(type) {
        if (type === 'admin') return '';
        if (type === 'storeOwner') return '';
        if (type === 'clerk') return '';
    }

    static getTypeToken(type) {
        if (type === '') return 'admin';
        if (type === '') return 'storeOwner';
        if (type === '') return 'clerk';
    }

    render() {
        const u = [];
        for (let i in this.state.users) {
            const user = this.state.users[i];
            u.push(
                <tr key={'user' + i}>
                    <td>{user.id}</td>
                    <td>{user.name}</td>
                    <td>{user.username}</td>
                    <td>{UserListPanel.getTypeString(user.type)}</td>
                    <td>
                        <button className="btn btn-link" onClick={
                            () => {
                                window.components.consoleFrame.pushBackOperation(
                                    '',
                                    () => {
                                        window.renderUserListPanel();
                                    }
                                );
                                window.renderUserInfoPanel();
                            }
                        }></button>
                        <button className="btn btn-link" onClick={() => {
                            window.components.consoleFrame.pushBackOperation(
                                '',
                                () => {
                                    window.renderUserListPanel();
                                }
                            );
                            window.renderUserInfoPanel();
                            window.components.userInfoPanel.setState({
                                state: 'edit'
                            });
                        }}></button>
                        <button className="btn btn-link" onClick={this.delete}></button>
                    </td>
                </tr>
            );
        }

        return (
            <div className="display-panel">
                <div className="display-title"></div>
                <div className="input-group input-group-sm" id="search-group">
                    <span className="input-group-btn">
                        <button className="btn btn-primary"><span className=" glyphicon glyphicon-search"/> </button>
                    </span>
                    <input type="text" ref="searchText" className="form-control form-inline" placeholder="//"/>
                </div>
                <table className="table" id="data-table">
                    <thead>
                    <tr className="table-head">
                        <td style={{width: '10%'}}></td>
                        <td style={{width: '10%'}}></td>
                        <td style={{width: '10%'}}></td>
                        <td style={{width: '10%'}}></td>
                        <td style={{width: '15%'}}></td>
                    </tr>
                    </thead>
                    <tbody>
                    {u}
                    </tbody>
                </table>
            </div>
        )
    }

    componentDidMount() {
        // 
        this.refs.searchText.onchange = () => {
            this.filter(this.refs.searchText.value);
        };
    }

    filter(key) {
        if (key && key.trim()) {
            const users = [];
            this.state.allUsers.map(
                (e, i, all) => {
                    if ((e.id + '').indexOf(key) !== -1 ||
                        (e.name + '').indexOf(key) !== -1 ||
                        (e.username + '').indexOf(key) !== -1 ||
                        (UserListPanel.getTypeString(e.type) + '').indexOf(key) !== -1
                    ) {
                        users.push(e);
                    }
                    return null;
                }, this
            );
            this.setState({
                users: users
            });
        } else {
            this.setState({
                users: this.state.allUsers
            });
        }
    }

    delete() {
        window.showDialog(
            "",
            <YesNoDialog
                content=""
                yesOption={() => {
                    // 
                    window.closeDialog();
                }}
            />
        )
    }
}


